
.blogo{
    width: 312px;
    height: 312px;
}
.body{
    overflow: hidden;
}
h2{
color: lavender;
font-family: Poppins;
font-size: 40px;

}
.fbody{
    display: flex;
    justify-content: space-around;
    border-radius: 5rem 5rem 5rem 5rem;
}
.screen{
    width: 412px;
    height: 618px;
}
.fsbody{
    text-align: center;
    align-items: center;
}
.sbody{
    display: flex;
    justify-content: space-around;
  overflow: hidden;
    height: fit-content;
    background-color: whitesmoke;
}
.icon{
    font-size: 5rem;
    color: brown;
}
.ficon{
    width: 20%;
    text-align: center;
    padding: 2rem;
    border-radius: 20% ;
    background-color: rgb(63,82,95);
    
}
.ficon h1{
    font-size: 20px;
    color: antiquewhite;
    font-weight: 500;
    padding: 1rem;
}
.vbody{
    padding: 2rem;
}
iframe{
    margin-left: 25rem;
    height: 300px;
    width: 400px;
}
SwiperSlide{
    position: absolute;
}
.flbody img{
    box-shadow: 10px 10px 156px 66px rgba(63,82,95,0.75);
-webkit-box-shadow: 10px 10px 156px 66px rgba(63,82,95,0.75);
-moz-box-shadow: 10px 10px 156px 66px rgba(63,82,95,0.75);
}

@font-face{font-family:"fontfutura";src:url("https://cdn.rawgit.com/KompiAjaib/font/master/product-sans-regular.ttf") format("ttf");font-weight:normal;font-style:normal;}
a.btn-google{color:#fff}
.btn{padding:10px 16px;margin:5px;font-size:16px;line-height:3.3333333; border-radius:6px;text-align:center;white-space:nowrap;vertical-align:middle;-ms-touch-action:manipulation;touch-action:manipulation;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;border:1px solid transparent;font-weight:500;text-decoration:none;display:inline-block}
.btn:active:focus,.btn:focus{outline:0}
.btn:focus,.btn:hover{color:#333;text-decoration:none;outline:0}
.btn:active{outline:0;-webkit-box-shadow:inset 0 3px 5px rgba(0,0,0,.125);box-shadow:inset 0 3px 5px rgba(0,0,0,.125)}
.btn-google{color:#fff;background-color:#111;border-color:#000;padding:15px 16px 5px 40px;position:relative;font-family: 'Product Sans', Arial, sans-serif;font-weight:600}
.btn-google:focus{color:#fff;background-color:#555;border-color:#000}
.btn-google:active,.btn-google:hover{color:#fff;background-color:#555;border-color:#000;}
.btn-google:before{content:"";background-image:url(https://4.bp.blogspot.com/-52U3eP2JDM4/WSkIT1vbUxI/AAAAAAAArQA/iF1BeARv2To-2FGQU7V6UbNPivuv_lccACLcB/s30/nexus2cee_ic_launcher_play_store_new-1.png);background-size:cover;background-repeat:no-repeat;width:30px;height:30px;position:absolute;left:6px;top:50%;margin-top:-15px}
.btn-google:after{content:"GET IT ON";position:absolute;top:5px;left:40px;font-size:10px;font-weight:400}


.footer{
    background-color: #111;
    height: 5rem;
    margin-top: 3rem;
    color: antiquewhite;
    font-size: 20px;
}
.ffoot{
   display: flex;
   justify-content: space-between;
   margin: 1rem;
   padding-top: 1rem;
}
a{
    text-decoration: none;
    color: antiquewhite;
}
.nav{
    display: flex;
    justify-content: space-around;
    width: 40%;
}
a:hover{
    color: aquamarine;
}
@media only screen and (max-width: 1024px) {
    .fbody{
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .fsbody{
        width: 100%;
        font-size: large;
        text-align: center;
    }
    .fsbody img{
        width: 40%;
        height: 200px;
    }
    .flbody img{
        width: 90%;
padding-top: 1rem;
        margin-left: 1rem;
    }
    .ficon{
        width: 90%;
        margin: 1rem;
        margin-bottom: 2rem;
        overflow: hidden;
    }
    .sbody{
        
        display: flex;
        flex-direction: column;
        margin: auto;
        overflow: hidden;
        background-color: whitesmoke;
    }
    .icon{
        font-size: 2rem;
    }
    .vbody iframe {
        position: relative;
        right: 450px;
    }

    .ffoot{
        width: fit-content;
        font-size: 12px;
        font-style: italic;
        display: flex;
        justify-content: space-around;
    }
    .ffoot p{
       text-align: center;
        word-wrap: break-word;
    }
    .nav{
        display: flex;
        flex-direction: column;
        margin-left: 3rem;
    }
}